import { transformError as sdkTransformError } from '@wix/sdk-runtime/transform-error';
import {
  renameKeysFromSDKRequestToRESTRequest,
  renameKeysFromRESTResponseToSDKResponse,
} from '@wix/sdk-runtime/rename-all-nested-keys';
import { HttpClient, NonNullablePaths } from '@wix/sdk-types';
import * as ambassadorWixTipsStaffV1Staff from './tips-staff-v1-staff-tippable-staff.http.js';

/**
 * Information about a team member who's eligible to receive a portion of the tip.
 * Currently, only Bookings staff members ([SDK](https://dev.wix.com/docs/sdk/backend-modules/bookings/staff-members/introduction) | [REST](https://dev.wix.com/docs/rest/business-solutions/bookings/staff-members/staff-member-object))
 * and [site collaborators](https://support.wix.com/en/article/inviting-people-to-contribute-to-your-site)
 * are eligible for tip distributions.
 */
export interface Staff {
  /**
   * Staff ID. Matches `staffMemberId` if available, or
   * `identificationData.wixUserId` if not.
   * @format GUID
   * @readonly
   */
  _id?: string | null;
  /**
   * Staff name. Matches the name of the
   * Bookings staff member ([SDK](https://dev.wix.com/docs/sdk/backend-modules/bookings/staff-members/introduction) | [REST](https://dev.wix.com/docs/rest/business-solutions/bookings/staff-members/staff-member-object)),
   * if available.
   * @maxLength 500
   */
  name?: string | null;
  /**
   * Staff member ID. Available only if the staff is connected to a
   * Bookings staff member ([SDK](https://dev.wix.com/docs/sdk/backend-modules/bookings/staff-members/introduction) | [REST](https://dev.wix.com/docs/rest/business-solutions/bookings/staff-members/staff-member-object)).
   * @format GUID
   */
  staffMemberId?: string | null;
  /**
   * Identification data of the staff. Available only if the staff is a
   * [site collaborator](https://support.wix.com/en/article/inviting-people-to-contribute-to-your-site).
   */
  identificationData?: IdentificationData;
}

export interface IdentificationData extends IdentificationDataIdOneOf {
  /**
   * ID of a site visitor who hasn't logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor who has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user. For example, the site owner or a
   * [site collaborator](https://support.wix.com/en/article/inviting-people-to-contribute-to-your-site).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
  /**
   * ID of the contact in the site's [CRM by Ascend](https://www.wix.com/ascend/crm) system.
   * See the Contacts API for additional details.
   * @format GUID
   */
  contactId?: string | null;
}

/** @oneof */
export interface IdentificationDataIdOneOf {
  /**
   * ID of a site visitor who hasn't logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor who has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user. For example, the site owner or a
   * [site collaborator](https://support.wix.com/en/article/inviting-people-to-contribute-to-your-site).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
}

export enum IdentityType {
  UNKNOWN = 'UNKNOWN',
  ANONYMOUS_VISITOR = 'ANONYMOUS_VISITOR',
  MEMBER = 'MEMBER',
  WIX_USER = 'WIX_USER',
  APP = 'APP',
}

/** @enumType */
export type IdentityTypeWithLiterals =
  | IdentityType
  | 'UNKNOWN'
  | 'ANONYMOUS_VISITOR'
  | 'MEMBER'
  | 'WIX_USER'
  | 'APP';

export interface ListTippableStaffRequest {
  /**
   * Whether to include staff who are only Wix users and not
   * Bookings staff members ([SDK](https://dev.wix.com/docs/sdk/backend-modules/bookings/staff-members/introduction) | [REST](https://dev.wix.com/docs/rest/business-solutions/bookings/staff-members/staff-member-object))
   * in the response. Setting `{"includeWixUsers": true}` returns all tippable
   * staff, including Bookings staff, site collaborators, or those who are both.
   * By default, or if you provide `{"includeWixUsers": false}`, only Bookings
   * staff members or those who are both are returned.
   *
   * Default: `false`.
   */
  includeWixUsers?: boolean;
  /**
   * Filters the returned staff by name. If you provide a name as filter, only
   * staff whose names start with the given input are returned. The filter isn't
   * case sensitive.
   *
   * Max: 500 characters
   * @maxLength 500
   */
  filterByName?: string | null;
  /**
   * Filters the returned staff by ID. If you provide a list of IDs as filter,
   * only staff members with exact matching IDs are returned.
   *
   * Max: 100 IDs
   * @maxSize 100
   * @format GUID
   */
  filterByIds?: string[] | null;
}

export interface ListTippableStaffResponse {
  /**
   * Retrieved staff.
   * @maxSize 100
   */
  staff?: Staff[];
}

/**
 * Retrieves a list of up to 100 staff who are eligible to receive tips, given
 * the provided filtering.
 * @public
 * @documentationMaturity preview
 * @param options - Options to use when listing tippable staff.
 * @permissionId WIX_TIPS.STAFF_READ
 * @applicableIdentity APP
 * @fqn wix.tips.staff.v1.TipStaffService.ListTippableStaff
 */
export async function listTippableStaff(
  options?: ListTippableStaffOptions
): Promise<
  NonNullablePaths<
    ListTippableStaffResponse,
    | `staff`
    | `staff.${number}.identificationData.anonymousVisitorId`
    | `staff.${number}.identificationData.memberId`
    | `staff.${number}.identificationData.wixUserId`
    | `staff.${number}.identificationData.appId`,
    5
  >
> {
  // @ts-ignore
  const { httpClient, sideEffects } = arguments[1] as {
    httpClient: HttpClient;
    sideEffects?: any;
  };

  const payload = renameKeysFromSDKRequestToRESTRequest({
    includeWixUsers: options?.includeWixUsers,
    filterByName: options?.filterByName,
    filterByIds: options?.filterByIds,
  });

  const reqOpts = ambassadorWixTipsStaffV1Staff.listTippableStaff(payload);

  sideEffects?.onSiteCall?.();
  try {
    const result = await httpClient.request(reqOpts);
    sideEffects?.onSuccess?.(result);

    return renameKeysFromRESTResponseToSDKResponse(result.data)!;
  } catch (err: any) {
    const transformedError = sdkTransformError(
      err,
      {
        spreadPathsToArguments: {},
        explicitPathsToArguments: {
          includeWixUsers: '$[0].includeWixUsers',
          filterByName: '$[0].filterByName',
          filterByIds: '$[0].filterByIds',
        },
        singleArgumentUnchanged: false,
      },
      ['options']
    );
    sideEffects?.onError?.(err);

    throw transformedError;
  }
}

export interface ListTippableStaffOptions {
  /**
   * Whether to include staff who are only Wix users and not
   * Bookings staff members ([SDK](https://dev.wix.com/docs/sdk/backend-modules/bookings/staff-members/introduction) | [REST](https://dev.wix.com/docs/rest/business-solutions/bookings/staff-members/staff-member-object))
   * in the response. Setting `{"includeWixUsers": true}` returns all tippable
   * staff, including Bookings staff, site collaborators, or those who are both.
   * By default, or if you provide `{"includeWixUsers": false}`, only Bookings
   * staff members or those who are both are returned.
   *
   * Default: `false`.
   */
  includeWixUsers?: boolean;
  /**
   * Filters the returned staff by name. If you provide a name as filter, only
   * staff whose names start with the given input are returned. The filter isn't
   * case sensitive.
   *
   * Max: 500 characters
   * @maxLength 500
   */
  filterByName?: string | null;
  /**
   * Filters the returned staff by ID. If you provide a list of IDs as filter,
   * only staff members with exact matching IDs are returned.
   *
   * Max: 100 IDs
   * @maxSize 100
   * @format GUID
   */
  filterByIds?: string[] | null;
}
